<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>
		<b-form @submit.stop.prevent>
			<!--
	<b-form-group
		label="*ID"
		label-for="id_medicamento"
		@submit.stop.prevent="handleSubmit"
	>
		<b-form-input
			id="id_medicamento"
			v-model="medicamentos.id_medicamento"
			type="number"
			:state="validacion.id_medicamento.estado"
			placeholder="Ingrese un Numero"
			invalid-feedback="Complete este campo"
			required
		>
		</b-form-input>
		<b-form-invalid-feedback id="id_medicamento-live-feedback">
			{{ validacion.id_producto.mensaje }}
		</b-form-invalid-feedback>
	</b-form-group>
	-->

			<b-form-group label="*Nombre"
						  label-for="nombre"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="nombre"
							  v-model="productos.nombre"
							  type="text"
							  placeholder="Ingrese el nombre"
							  invalid-feedback="Complete este campo"
							  :state="validacion.nombre.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="nombre-live-feedback">
					{{ validacion.nombre.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Tipo de Producto"
						  label-for="tipo_producto"
						  @submit.stop.prevent="handleSubmit">
				<b-form-select id="tipo_producto"
							   v-model="productos.tipo_producto"
							   type="text"
							   placeholder="Describa el tipo de producto"
							   invalid-feedback="Complete este campo"
							   :state="validacion.tipo_producto.estado"
							   required
							   :options="tipos_productos">
				</b-form-select>
				<b-form-invalid-feedback id="tipo_producto-live-feedback">
					{{ validacion.tipo_producto.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Proveedor"
						  label-for="letra"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="letra"
							   v-model="productos.letra"
							   type="text"
							   placeholder="Ingrese una Letra"
							   invalid-feedback="Complete este campo"
							   required
								solo
								filled
							   :state="validacion.letra.estado"
							   :items="options">
				</v-autocomplete>
				<b-form-invalid-feedback id="numero_producto-live-feedback">
					{{ validacion.letra.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Numero de producto"
						  label-for="numero_producto"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="numero_producto"
							  v-model="productos.numero_producto"
							  type="text"
							  :state="validacion.numero_producto.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
					<b-form-invalid-feedback id="input-live-feedback">
						Debe ingresar al menos 1 numero.
					</b-form-invalid-feedback>
				</b-form-input>
				<b-form-invalid-feedback id="letra-live-feedback">
					{{ validacion.letra.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			
		</b-form>
		<b-button class="mt-2" variant="success" bk @click="postProducto()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				productos: {},
				productos2: {},
                productos3: {},
				respuesta: {},
				proveedores: {},
				data: {},

				options: [{ value: null, text: "Elija un proveedor", disabled: true }],
				tipos_productos: [
					{ value: null, text: "Elija un tipo de producto", disabled: true },
                    { value: "Pollo", text: "Pollo"},
                    { value: "Congelados", text: "Congelado"}
				],

				text: "",
				validacion: {
					id_producto: { estado: null, mensaje: "" },
					nombre: { estado: null, mensaje: "" },
					tipo_producto: { estado: null, mensaje: "" },
					letra: { estado: null, mensaje: "" },
					numero_producto: { estado: null, mensaje: "" },
                    stocktotal: { estado: null, mensaje: "" },
				},
			};
		},

		methods: {
			async getProveedores() {
				let proveedorAPI = new APIControler();
                proveedorAPI.apiUrl.pathname = "proveedores/";
                this.data = await proveedorAPI.getData(this.proveedores);
				this.data.forEach((element) => {
					let option = {};
					option.value =
                        "https://arandadistribucion.pythonanywhere.com/proveedores/" + element.codigo_proveedor + "/";
                    option.text = element.codigo_proveedor + " - " + element.nombre + " - " + element.letra;
					console.log(option);
                    this.options.push(option);
				});
			},
			/*
    async getMedicamento() {
      let medicamentosAPI = new APIControler();
      this.data = await medicamentosAPI.getData();
    },*/

			async postProducto() {
				let productosAPI = new APIControler();
				productosAPI.apiUrl.pathname = "productos/";
				if (this.productos.tipo_producto == "Pollo") {
					console.log("Entre");
					this.productos2 = Object.assign({},this.productos);
                    this.productos3 = Object.assign({}, this.productos);
                    this.productos2.nombre = this.productos2.nombre + " P2";
                    this.productos3.nombre = this.productos3.nombre + " P3";
					this.productos.nombre = this.productos.nombre + " P1";
                    //this.productos2.numero_producto = parseInt(this.productos2.numero_producto) + 1;
					//this.productos3.numero_producto = parseInt(this.productos3.numero_producto) + 2;
					this.respuesta = await productosAPI.postData(this.productos);
					this.respuesta = await productosAPI.postData(this.productos2);
                    this.respuesta = await productosAPI.postData(this.productos3);
				} else
				{
                    this.respuesta = await productosAPI.postData(this.productos);
				}
				//this.data = await medicamentosAPI.postData(this.medicamentos);
				this.cargarFeedback();
				this.resetForm();

				this.updateTable();
			},

			async resetForm() {
                this.productos.id_producto = null;
                this.productos.nombre = "";
                this.productos.tipo_producto = "";
                this.productos.letra = "";
                this.productos.numero_producto = "";
			},

			cargarFeedback() {
				let valido;
				if (!this.respuesta) this.respuesta = {};
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
		beforeMount() {
			this.getProveedores();
		},
		/*
  computed: {
      validation() {
        return this.text.length > 0 ? true : false;
      },
    }
  */
	};
</script>

<style></style>
