<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>
		<b-form @submit.stop.prevent>
			<b-form-group label="*ID"
						  label-for="id_producto"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="id_producto"
							  v-model="item_med.id_producto"
							  type="number"
							  :state="validacion.id_producto.estado"
							  placeholder="Ingrese un Numero"
							  invalid-feedback="Complete este campo"
							  disabled
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="id_producto-live-feedback">
					{{ validacion.id_producto.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Nombre"
						  label-for="nombre"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="nombre"
							  v-model="item_med.nombre"
							  type="text"
							  placeholder="Ingrese el nombre"
							  invalid-feedback="Complete este campo"
							  :state="validacion.nombre.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="nombre-live-feedback">
					{{ validacion.nombre.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Tipo de Producto"
						  label-for="tipo_producto"
						  @submit.stop.prevent="handleSubmit">
				<b-form-select id="tipo_producto"
							   v-model="item_med.tipo_producto"
							   type="text"
							   placeholder="Describa el tipo de producto"
							   invalid-feedback="Complete este campo"
							   :state="validacion.tipo_producto.estado"
							   required
							   :options="tipos_productos">
				</b-form-select>
				<b-form-invalid-feedback id="tipo_producto-live-feedback">
					{{ validacion.tipo_producto.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Proveedor"
						  label-for="letra"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="letra"
							   v-model="item_med.letra"
							   type="text"
							   placeholder="Ingrese una Letra"
							   invalid-feedback="Complete este campo"
							   required
								solo
								filled
							   :state="validacion.letra.estado"
							   :items="options">
				</v-autocomplete>
				<b-form-invalid-feedback id="numero_producto-live-feedback">
					{{ validacion.letra.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Numero de producto"
						  label-for="numero_producto"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="numero_producto"
							  v-model="item_med.numero_producto"
							  type="text"
							  :state="validacion.numero_producto.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
					<b-form-invalid-feedback id="input-live-feedback">
						Debe ingresar al menos 1 numero.
					</b-form-invalid-feedback>
				</b-form-input>
				<b-form-invalid-feedback id="letra-live-feedback">
					{{ validacion.letra.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Stock Total"
						  label-for="stocktotal"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="stocktotal"
							  v-model="item_med.stocktotal"
							  type="text"
							  :state="validacion.stocktotal.estado"
							  placeholder="Ingrese un numero"
							  invalid-feedback="Complete este campo"
							  aria-describedby="input-live-help input-live-feedback"
							  required>
					<b-form-invalid-feedback id="input-live-feedback">
						Debe ingresar al menos 1 numero.
					</b-form-invalid-feedback>
				</b-form-input>
				<b-form-invalid-feedback id="stocktotal-live-feedback">
					{{ validacion.stocktotal.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>

		<b-button class="mt-2" variant="success" bk @click="putProducto()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
	import axios from "axios";
	1;
	export default {
		props: {
			item_med: {},
			updateTable: Function,
		},
		data() {
			return {
				// medicamentos: {},
				proveedores: {},
				data: {},
				respuesta: {},
				options: [{ value: null, text: "Elija un proveedor", disabled: true }],
                tipos_productos: [
                    { value: null, text: "Elija un tipo de producto", disabled: true },
                    { value: "Pollo", text: "Pollo" },
                    { value: "Congelados", text: "Congelado" }
                ],
				validacion: {
					id_producto: { estado: null, mensaje: "" },
					nombre: { estado: null, mensaje: "" },
					tipo_producto: { estado: null, mensaje: "" },
					letra: { estado: null, mensaje: "" },
					numero_producto: { estado: null, mensaje: "" },
                    stocktotal: { estado: null, mensaje: "" },
				},
			};
		},
		created: function () {
			//console.log('Funcion realizada');
			this.getProveedores();
		},
		methods: {
            async getProveedores() {
                let proveedorAPI = new APIControler();
                proveedorAPI.apiUrl.pathname = "proveedores/";
                this.data = await proveedorAPI.getData(this.proveedores);
                this.data.forEach((element) => {
                    let option = {};
                    option.value =
                        "http://alhost:8081/proveedores/" + element.codigo_proveedor + "/";
                    option.text = element.codigo_proveedor + " - " + element.nombre + " - " + element.letra;
                    console.log(option);
                    this.options.push(option);
                });
			},

			async putProducto() {
				let resp = "vacio";
				await axios
					.put(
						"http://alhost:8081/productos/" +
							this.item_med.id_producto +
							"/",
						this.item_med
					)
					.then(function () {
						swal("Operación Exitosa", " ", "success");
					})
					.catch(function (error) {
						const mje =
							error.response.status < 500
								? "Los datos no son válidos"
								: "Se ha detectado un problema ";
						swal("¡ERROR!", mje, "error");
						resp = error.response.data;
					});
				this.respuesta = resp;
				this.cargarFeedback();
				this.updateTable();
			},
			cargarFeedback() {
				let valido;
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
	};
</script>

<style></style>
